import React from 'react';
import classNames from 'classnames';
// import Animate from 'rc-animate';
import PureRenderMixin from 'rc-util/lib/PureRenderMixin';
import { FixedSizeList as List } from 'react-window';
import Checkbox from '../checkbox';
import Search from './search';
import Item from './item';
// import triggerEvent from '../_util/triggerEvent';
function noop() {
}
function isRenderResultPlainObject(result) {
    return result && !React.isValidElement(result) &&
        Object.prototype.toString.call(result) === '[object Object]';
}
export default class TransferList extends React.Component {
    constructor(props) {
        super(props);
        this.handleSelect = (selectedItem) => {
            const { checkedKeys } = this.props;
            const result = checkedKeys.some((key) => key === selectedItem.key);
            this.props.handleSelect(selectedItem, !result);
        };
        this.handleFilter = (e) => {
            this.props.handleFilter(e);
            if (!e.target.value) {
                return;
            }
            // Manually trigger scroll event for lazy search bug
            // https://github.com/ant-design/ant-design/issues/5631
            // this.triggerScrollTimer = window.setTimeout(() => {
            //   const dom: any = findDOMNode(this);
            //   if (dom) {
            //     const listNode = dom.querySelectorAll('.w-transfer-list-content')[0];
            //     if (listNode) {
            //       triggerEvent(listNode, 'scroll');
            //     }
            //   }
            // }, 0);
        };
        this.handleClear = () => {
            this.props.handleClear();
        };
        this.matchFilter = (text, item) => {
            const { filter, filterOption } = this.props;
            if (filterOption) {
                return filterOption(filter, item);
            }
            return text.indexOf(filter) >= 0;
        };
        this.renderItem = (item) => {
            const { render = noop } = this.props;
            const renderResult = render(item);
            const isRenderResultPlain = isRenderResultPlainObject(renderResult);
            return {
                renderedText: isRenderResultPlain ? renderResult.value : renderResult,
                renderedEl: isRenderResultPlain ? renderResult.label : renderResult,
            };
        };
        this.state = {
            mounted: false,
            contentHeight: 0
        };
        this.myRef = React.createRef();
    }
    componentDidMount() {
        const { current } = this.myRef;
        const { prefixCls } = this.props;
        if (current) {
            const listContent = current.querySelector(`.${prefixCls}-content`);
            if (listContent) {
                this.setState({
                    contentHeight: listContent.clientHeight
                });
            }
        }
        //w-transfer-list-content
        this.timer = window.setTimeout(() => {
            this.setState({
                mounted: true,
            });
        }, 0);
    }
    componentWillUnmount() {
        clearTimeout(this.timer);
        clearTimeout(this.triggerScrollTimer);
    }
    shouldComponentUpdate(...args) {
        return PureRenderMixin.shouldComponentUpdate.apply(this, args);
    }
    getCheckStatus(filteredDataSource) {
        const { checkedKeys } = this.props;
        if (checkedKeys.length === 0) {
            return 'none';
        }
        else if (filteredDataSource.every(item => checkedKeys.indexOf(item.key) >= 0)) {
            return 'all';
        }
        return 'part';
    }
    render() {
        const { prefixCls, dataSource, titleText, checkedKeys, lazy, body = noop, footer = noop, showSearch, style, filter, searchPlaceholder, notFoundContent, selectAllText, } = this.props;
        const { contentHeight } = this.state;
        // Custom Layout
        const footerDom = footer(Object.assign({}, this.props));
        const bodyDom = body(Object.assign({}, this.props));
        const listCls = classNames(prefixCls, {
            [`${prefixCls}-with-footer`]: !!footerDom,
        });
        const filteredDataSource = [];
        const totalDataSource = [];
        const showItems = [];
        dataSource.forEach((item) => {
            const { renderedText, renderedEl } = this.renderItem(item);
            if (filter && filter.trim() && !this.matchFilter(renderedText, item)) {
                return null;
            }
            // all show items
            totalDataSource.push(item);
            if (!item.disabled) {
                // response to checkAll items
                filteredDataSource.push(item);
            }
            const checked = checkedKeys.indexOf(item.key) >= 0;
            showItems.push({
                key: item.key,
                item,
                renderedText,
                renderedEl,
                checked,
                prefixCls,
                onClick: this.handleSelect
            });
        });
        // const unit = dataSource.length > 1 ? itemsUnit : itemUnit;
        const search = showSearch ? (<div className={`${prefixCls}-body-search-wrapper`}>
        <Search prefixCls={`${prefixCls}-search`} onChange={this.handleFilter} handleClear={this.handleClear} placeholder={searchPlaceholder} value={filter}/>
      </div>) : null;
        const listBodyCls = classNames(`${prefixCls}-body`, {
            [`${prefixCls}-body-with-search`]: showSearch,
            [`${prefixCls}-body-no-content`]: showItems.length === 0
        });
        const listBody = bodyDom || (<div className={listBodyCls}>
        {search}
        
        <div className={`${prefixCls}-content`}>
          {contentHeight > 0 ?
            <List height={contentHeight} itemCount={showItems.length} itemSize={lazy && lazy.height}>
              {({ index, style }) => {
                const item = showItems[index];
                return (<Item {...item} style={style}/>);
                // <div key={showItems[index].key} style={style}>{showItems[index]}</div>
            }}
            </List> : null}
        </div>
        <div className={`${prefixCls}-body-not-found`}>
          {notFoundContent}
        </div>
      </div>);
        const listFooter = footerDom ? (<div className={`${prefixCls}-footer`}>
        {footerDom}
      </div>) : null;
        const checkStatus = this.getCheckStatus(filteredDataSource);
        const checkedAll = checkStatus === 'all';
        const checkAllCheckbox = (<Checkbox ref="checkbox" checked={checkedAll} indeterminate={checkStatus === 'part'} onChange={() => this.props.handleSelectAll(filteredDataSource, checkedAll)}>
        <span className={`${prefixCls}-header-selected`}>
          {selectAllText} (<span>
            {`${checkedKeys.length}/` + totalDataSource.length}
          </span>)
        </span>
      </Checkbox>);
        return (<div className={listCls} style={style} ref={this.myRef}>
        <div className={`${prefixCls}-header`}>
          {checkAllCheckbox}
          <span className={`${prefixCls}-header-title`}>
            {titleText}
          </span>
        </div>
        {listBody}
        {listFooter}
      </div>);
    }
}
TransferList.defaultProps = {
    dataSource: [],
    titleText: '',
    showSearch: false,
    render: noop,
    lazy: { height: 32 },
};
